import React from 'react'
import { hashHistory } from 'react-router'
import ComonentHeader from '.header'
class UserInfo extends React.Component {
    render() {
       <div>
           <ComonentHeader/>
       </div>
    }
    clickHandler(value) {
        hashHistory.push('/detail/' + value)
    }
}

export default UserInfo


